@keyframes initAnima{0%{opacity:0;transform:translateY(20px)}100%{opacity:1;transform:translateY(0)}}:root{--background: #fff0f0;--sub-background: #f2e5e5;--stroke-color: #ede1e1;--txt-color1: #ff7474;--txt-color2: #2f2f2f;--txt-st: #bd5b5b;--thumbnail-ico: #af6666;--green: #00a349;--red: #ff3f3f}[data-theme="dark"]{--background: #201f1f;--sub-background: #272626;--stroke-color: #302e2e;--txt-color1: #ff7474;--txt-color2: #ffffff;--txt-st: #bd5b5b;--thumbnail-ico: #af6666;--green: #2cd478;--red: #ff7474}body{margin:0;font-family:"Poppins", sans-serif;background:#fff0f0;background:var(--background);overflow-x:hidden;transition:background 300ms}input{width:calc(100% - 7.25rem);font-family:"Poppins";font-weight:600;font-size:1.2rem;line-height:42px;color:#2f2f2f;color:var(--txt-color2);background:#f2e5e5;background:var(--sub-background);border:3px solid #ede1e1;border:3px solid var(--stroke-color);border-radius:5px;padding:0.5rem 3.5rem;outline:none;transition:border-color 300ms, background 300ms, color 300ms}.switch{position:relative;background:#2f2f2f;background:var(--txt-color2);width:3rem;height:1.5rem;border-radius:100px;cursor:pointer;transition:background 300ms}.switch::after{position:absolute;content:"";width:15px;height:15px;border-radius:100px;top:50%;transform:translateY(-50%);margin:0rem 0.4rem;background:#fff0f0;background:var(--background);transition:transform 300ms}.switch.on{background:#ff7474;background:var(--txt-color1)}.switch.on::after{transform:translate(21px, -50%)}.status{position:absolute;background:#00a349;background:var(--green);display:flex;align-items:center;grid-gap:1rem;gap:1rem;padding:0.5rem 0.8rem;padding-right:2.5rem;width:calc(100% - 3.3rem);color:#fff0f0;color:var(--background);overflow:hidden;transition:background 300ms;left:0;right:0;margin-top:1rem;border-radius:5px;font-weight:600;font-size:0.9rem}.status>i{font-size:1.5rem;font-weight:normal}.status>div{word-break:break-all}.status>*{transition:color 300ms}.status button{cursor: