@keyframes start{0%{opacity:0;transform:translateX(-100%)}100%{opacity:1;transform:translateX(0)}}.loadingSection{min-height:calc(100vh - 2rem);width:calc(100vw - 3.04rem);padding:1rem;display:grid;place-content:center;animation:start 1000ms ease 0ms 1}.loadingSection img{background:transparent}

.nav{height:4rem;padding:1rem 2rem;background:var(--background);position:fixed;top:0;left:0;right:0;display:flex;align-items:center;justify-content:space-between;z-index:1;transition:background 300ms}.nav .site_pref{display:flex;align-items:center;grid-gap:2rem;gap:2rem}.nav .site_pref .site_theme{position:relative;background:var(--txt-color1);width:3rem;height:1.5rem;border-radius:100px;cursor:pointer;transition:background 300ms}.nav .site_pref .site_theme::after{position:absolute;content:"";width:15px;height:15px;border-radius:100px;top:50%;transform:translateY(-50%);margin:0rem 0.4rem;background:var(--background);transition:transform 300ms}.nav .site_pref .site_theme.dark{background:var(--stroke-color)}.nav .site_pref .site_theme.dark::after{background:var(--txt-color1);transform:translate(21px, -50%)}.nav .site_pref .pref{display:grid;place-content:center;color:var(--txt-color2);cursor:pointer;transition:color 300ms}.nav .site_pref .pref:hover{color:var(--txt-color1)}.nav .site_pref .pref i{font-size:1.7rem}.site_title{cursor:pointer}.site_title,.availableTitle{font-weight:800;text-transform:uppercase;font-size:1.2rem;letter-spacing:6px;word-spacing:0.4rem;color:var(--txt-color2);transition:color 300ms}.site_title span,.availableTitle span{color:var(--txt-color1)}

.prefModal{position:fixed;inset:0;background:#0000005d;z-index:2;display:grid;place-items:center}.prefModal .prefContainer{pointer-events:all;width:calc(100% - 4rem);max-width:600px;min-height:100px;margin:2rem;background:var(--background);border-radius:5px}.prefModal .prefContainer .prefHeader{display:flex;align-items:center;justify-content:space-between;padding:1rem;color:var(--txt-color2)}.prefModal .prefContainer .prefHeader:first-child{font-size:1.7rem;font-weight:bold}.prefModal .prefContainer .prefHeader button{cursor:pointer;border:none;font-size:2rem;background:transparent;display:grid;place-content:center;aspect-ratio:1 / 1;border-radius:5px;color:var(--txt-color2)}.prefModal .prefContainer .prefHeader button:hover{background:var(--sub-background)}.prefModal .prefContainer .prefList{padding:1rem;display:flex;flex-direction:column;grid-gap:1.5rem;gap:1.5rem}.prefModal .prefContainer .prefList .prefItem{display:flex;align-items:center;justify-content:space-between;grid-gap:1rem;gap:1rem}.prefModal .prefContainer .prefList .prefItem .prefItemAction>:not(.switch){width:140px}.prefModal .prefContainer .prefList .prefItem.disabled{opacity:0.3;-webkit-filter:grayscale(1);filter:grayscale(1);pointer-events:none;-webkit-user-select:none;-ms-user-select:none;user-select:none}.prefModal .prefContainer .prefList .prefTitle{font-weight:bold;text-transform:uppercase;color:var(--txt-color2);word-spacing:0.2rem}.prefModal .prefContainer .prefList .prefTitle span{color:var(--thumbnail-ico)}.prefModal .prefContainer .prefList .prefDesc{margin-top:0.5rem;max-width:35ch;font-size:0.9rem;opacity:0.9;color:var(--txt-color2)}

.searchSection{min-height:calc(100vh - 2rem);width:calc(100% - 2rem);padding:1rem;display:flex;flex-direction:row;align-items:center;justify-content:center;grid-gap:5rem;gap:5rem}.searchSection>i{font-size:10rem;color:var(--thumbnail-ico)}.isearchWrapper{position:relative;max-width:35rem;width:100%}@media only screen and (max-width: 816px){.searchSection{grid-gap:2rem;gap:2rem}}@media only screen and (max-width: 600px){.searchSection{flex-direction:column;grid-gap:2rem;gap:2rem}}.search{position:relative;width:inherit}.search button{position:absolute;width:3.5rem;background:transparent;border:none;font-size:1.6rem;display:grid;place-content:center}.search .searchIco{left:0;top:0;bottom:0;color:var(--txt-st);pointer-events:none}.search input:focus ~ .searchIco{-webkit-filter:contrast(1.6);filter:contrast(1.6)}.search input:not(:focus) ~ .searchClearIco{opacity:0;cursor:default}.search .searchClearIco{right:0;top:0;bottom:0;cursor:pointer;color:var(--txt-color2);transition:opacity 300ms, transform 300ms ease}.search .searchClearIco:hover{color:var(--txt-color1)}.search .searchClearIco:active{transform:scale(0.9)}

.videoInfoSection{min-height:100vh;width:calc(100vw - 2rem);padding:1rem;display:flex;flex-direction:column;flex-wrap:wrap;align-items:center;justify-content:flex-start;grid-gap:5rem;gap:5rem;padding-top:8rem;z-index:-1}.videoInfoSection .searchWrapper{max-width:850px;width:100%;animation:initAnima 1000ms 0ms 1 forwards;opacity:0}.videoInfoSection .search{max-width:inherit;width:inherit}.videoInfoSection .search input{width:calc(100% - 7.35rem)}.videoInfoSection .infoContainer{max-width:850px;width:100%;display:flex;grid-gap:2rem;gap:2rem}.videoInfoSection .infoContainer .thumbnailContainer{position:relative;display:grid;place-content:center;width:75%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;aspect-ratio:16 / 9;background:var(--sub-background);border-radius:10px;font-size:7rem;color:var(--thumbnail-ico);overflow:hidden;animation:initAnima 1000ms 50ms 1 forwards;opacity:0}.videoInfoSection .infoContainer .thumbnailContainer .thumbnail{position:absolute;inset:0;background-image:url("https://i.ytimg.com/vi/qJeKZkK31JE/hq720.jpg?sqp=-oaymwEcCOgCEMoBSFXyq4qpAw4IARUAAIhCGAFwAcABBg==&rs=AOn4CLDGhl_b4P2KuvHMUQVmCjHOSSzeyQ");background-position:center;background-size:cover}.videoInfoSection .infoContainer .videoInfo{width:100%;display:flex;flex-direction:column;grid-gap:0.5rem;gap:0.5rem}.videoInfoSection .infoContainer .videoInfo .videoChannel{font-weight:700;text-transform:uppercase;letter-spacing:1px;color:var(--txt-color2);opacity:0.8;animation:initAnima 1000ms 100ms 1 forwards;opacity:0;transition:color 300ms}.videoInfoSection .infoContainer .videoInfo .videoTitle{font-weight:bold;font-size:1.8rem;color:var(--txt-color2);overflow:hidden;text-overflow:ellipsis;display:-webkit-box;-webkit-line-clamp:2;-webkit-box-orient:vertical;animation:initAnima 1000ms 200ms 1 forwards;opacity:0;transition:color 300ms}.videoInfoSection .infoContainer .videoInfo .videoDuration{background:var(--txt-color1);color:var(--background);width:-webkit-max-content;width:-moz-max-content;width:max-content;padding:0.25rem 0.8rem 0.25rem 0.3rem;border-radius:100px;display:flex;flex-direction:row;align-items:center;justify-content:center;grid-gap:0.4rem;gap:0.4rem;font-weight:bold;font-size:1rem;letter-spacing:2px;animation:initAnima 1000ms 300ms 1 forwards;opacity:0;transition:color 300ms}.videoInfoSection .infoContainer .videoInfo .videoDuration>i{font-weight:normal;font-size:1.7rem}.videoInfoSection .infoContainer .videoInfo .availableTitle{margin-top:2rem;letter-spacing:4px;font-size:1.1rem;animation:initAnima 1000ms 400ms 1 forwards;opacity:0}.videoInfoSection .infoContainer .videoQualityList{display:flex;flex-direction:column;grid-gap:1.5rem;gap:1.5rem;margin-top:1.2rem;animation:initAnima 1000ms 500ms 1 forwards;opacity:0}@media only screen and (max-width: 600px){.videoInfoSection .infoContainer{flex-direction:column}.videoInfoSection .infoContainer .thumbnailContainer{width:100%}}

.qualityCard{position:relative;background:var(--sub-background);border-radius:5px;display:flex;align-items:center;grid-gap:1.5rem;gap:1.5rem;padding:0.7rem;cursor:pointer;transition:transform 300ms, background 300ms}.qualityCard>*{-webkit-user-select:none;-ms-user-select:none;user-select:none}.qualityCard .downldIco{display:grid;place-content:center;width:3.7rem;aspect-ratio:1 / 1;background:var(--txt-color1);border-radius:5px;font-size:2rem;color:var(--background);transition:color 300ms}.qualityCard .resolution{font-weight:700;font-size:1.8rem;letter-spacing:3px;color:var(--txt-color2);transition:color 300ms}.qualityCard .format{position:absolute;top:50%;transform:translateY(-50%);right:0;margin-right:1rem;font-weight:800;font-size:2rem;letter-spacing:2px;color:var(--txt-st);opacity:0.5;transition:color 300ms}.qualityCard:hover{background:var(--txt-color1);transform:scale(1.05)}.qualityCard:hover .resolution,.qualityCard:hover .format{color:var(--background)}.qualityCard:active{background:var(--txt-color1);transform:scale(0.95)}.qualityCard:active .resolution,.qualityCard:active .format{color:var(--background)}

